*{
  box-sizing: border-box;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }

  to {
    transform: rotate(360deg);
  }
}

nav {
  display: flex;
  align-items: center;
  background-color: #ffffff;
  padding: 10px 25px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.25);
  z-index: 1;
}

.nav--logo_icon {
  width: 80px;
  margin-right: 10px;
}

.nav--logo_text {
  color: salmon;
  text-shadow: 0 0 5px lime;
  margin-right: auto;
  font-size: 30px;
}

.nav--title {
  color: #DEEBF8;
}

.nav--link {
  text-decoration: none;
}

.connectWalletBtn {
  width: 200px;
  height: 40px;
  font-size: 20px;
  font-weight: bold;
  background-color: salmon;
  border-radius: 10px;
  cursor: pointer;
}

.connectWalletBtn:hover {
  background-color: teal;
  color: antiquewhite;
}

.connectWalletBtn:active {
  color: yellowgreen;
}

.lottery-container {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin: 20px auto;
  background-color: #282c34;
  width: 90vw;
  max-width: 100%;
  border-radius: 20px;
  padding: 20px;
  box-shadow: inset 0 0 5px 2px rgba(123, 255, 0, 0.5);
  color: antiquewhite;
}

/* Round */
.round-container {
  display: flex;
  justify-content: center;
  flex-flow: row wrap;
  gap: 25px 20px;
  width: 100%;
}

.round {
  background-color: teal;
  border-radius: 10px;
  padding: 10px 15px;
  display: flex;
  width: 110px;
  box-sizing: border-box;
  flex-flow: column nowrap;
  align-items: center;
  justify-content: center;
  font-size: 1em;
  border: 2px solid transparent;
}

.round:hover {
  cursor: pointer;
  border: 2px groove salmon;
}

.round.selected {
  border: 2px double salmon;
  font-weight: bold;
}

.round:active {
  background-color: lightseagreen;
}

.round .ticket-price {
  font-size: 14px;
  font-style: italic;
}

.round.open {
  color: gold;
}

.round.close {
  color: silver;
}

.divider {
  width: 100%;
  border-top: 2px solid whitesmoke;
  margin: 20px 0;
}

/* Round Info Component */
.round-info {
  width: 100%;
  display: flex;
  text-align: left;
  flex-direction: column;
  align-items: flex-start;
  font-size: 20px;
  margin-bottom: 20px;
  padding: 0 20px;
}

.status-message{
  color: salmonD
}

.winner-container{
  display: flex;
}

.winner-container .winner-title{
  margin-right: 10px;
}

.round-number-container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  /* display: grid;
  grid-template-columns: repeat(10, 50px); */
  gap: 15px;
  margin: 20px 0;
}

.round-number {
  display: flex;
  width: 50px;
  height: 50px;
  justify-content: center;
  align-items: center;
  background-color: bisque;
  border-radius: 10px;
  color: teal;
  font-size: 23px;
  font-weight: bold;
}

.round-number:hover {
  cursor: pointer;
}

.round-number:not(.bought):hover {
  box-shadow: inset 0 0 5px 2px red;
}

.round-number.selected {
  background-color: limegreen;
  color: purple
}

.round-number.bought {
  background-color: silver;
}

.select-data{
  margin: 10px 0;
  font-size: 20px;
}

/* Buy Button */
.buy-btn{
  padding: 10px 20px;
  background-color: aquamarine;
  border-radius: 10px;
  color: brown;
  font-weight: bold;
  font-size: 20px;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

.buy-btn:hover{
  cursor: pointer;
  box-shadow: inset 0 0 3px 2px lightseagreen;

  background-color: gray;
  color: aliceblue;
}

.buy-btn:active{
  background-color: lightskyblue;
}

.buy-btn.disable{
  background-color: gray;
  color: aliceblue;
}

.buy-btn.disable:hover{
  cursor: default;
  box-shadow: none;
}